.club-services {
  @include listReset();
  display: flex;
  flex-wrap: wrap;
  background-color: $blue-color;
  padding: 30px 15px;
  margin: 1rem 0;
  border-radius: $borderRadius;

  @media (min-width: $xs) {
    padding: 30px;
  }

  &__item {
    flex-grow: 1;
    position: relative;
    padding-left: 35px;
    margin-bottom: 30px;
    width: 100%;

    @media (min-width: $xs) {
      width: calc(50% - 1rem);

      &:not(:nth-child(2n)) {
        margin-right: 1rem;
      }
    }
    @media (min-width: 800px) {
      width: calc(100% / 3 - 1rem);

      max-width: 295px;

      &:not(:nth-child(2n + 1)) {
        margin-right: 1rem;
      }
    }

    @media (min-width: $xl) {
      width: calc(25% - 1rem);
      max-width: 100%;

      &:not(:nth-child(4n)) {
        margin-right: 1rem;
      }
    }

    &::before {
      content: '';
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: 0 0;
    }

    &--1 {
      &::before {
        background-image: url('../img/icons-service/gym.svg');
      }
    }
    &--2 {
      &::before {
        background-image: url('../img/icons-service/group.svg');
      }
    }
    &--3 {
      &::before {
        background-image: url('../img/icons-service/cardio.svg');
      }
    }
    &--4 {
      order: 2;
      &::before {
        background-image: url('../img/icons-service/star.svg');
      }
    }
    &--5 {
      &::before {
        background-image: url('../img/icons-service/Swim.svg');
      }
    }
    &--6 {
      &::before {
        background-image: url('../img/icons-service/spa.svg');
      }
    }
    &--7 {
      &::before {
        background-image: url('../img/icons-service/kids.svg');
      }
    }
    &--8 {
      order: 2;
      &::before {
        background-image: url('../img/icons-service/ph_sun-fill.svg');
      }
    }
  }

  &__title {
    margin-bottom: 4px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
  }
}
