.clubs-info {
  border-top: 1px solid;
  border-bottom: 1px solid;
  // margin: 2rem 0;

  &__item {
    flex-grow: 1;
    position: relative;
    padding-top: 24px;
    padding-bottom: 24px;
    color: $primary-color;
    padding-left: calc(20px + 30px);

    &::before {
      content: '';
      position: absolute;
      top: 24px;
      left: 20px;
      // transform: translateY(-50%);
      width: 17px;
      height: 30px;
      background-image: url('../img/icons/fh.svg');
    }

    &:not(:last-child) {
      border-bottom: 1px solid;
    }

    @media (min-width: $md) {
      padding-left: calc(40px + 30px);

      &::before {
        top: 24px;
        left: 40px;
      }
    }
  }

  &__club {
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        375px: 15px,
        768px: 20px,
      )
    );
  }

  &__text {
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        375px: 16px,
        768px: 20px,
      )
    );
  }
}
