.subscription {
  display: flex;
  flex-direction: column;
  margin: 1rem 0 3rem;
  padding: 30px 15px;
  background-color: $blue-color;

  @media (min-width: $xs) {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
    padding: 30px;
  }
  @media (min-width: $xl) {
    padding: 40px 80px;
    margin: 20px auto 3rem;
    max-width: 1150px;
  }

  &__title {
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    @media (min-width: $md) {
      // width: calc(100% / 2 - 1rem);
      max-width: 365px;
      width: 100%;
      padding-top: 2rem;
      margin-right: 45px;
    }

    @media (min-width: $md) {
      width: calc(35% - 60px);
    }
    @media (min-width: $xl) {
      width: calc(35% - 45px);
    }
    @media (min-width: $xxl) {
      width: calc(40% - 45px);
    }
  }

  & .form-subscription {
    @media (min-width: $md) {
      width: calc(100% / 2 - 1rem);
    }
    @media (min-width: $md) {
      width: calc(65% - 1rem);
    }
    @media (min-width: $xxl) {
      width: 60%;
    }
  }
}
