.form-book {
  padding: 2rem 1rem;

  @media (min-width: $xs) {
    padding: 35px;
  }
  @media (min-width: $xl) {
    padding: 35px;
    padding-right: 55px;
  }

  & .title {
    margin-bottom: 1rem;
    padding-right: 1rem;

    font-size: 20px;
    line-height: 130%;
  }

  &__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    align-items: flex-start;

    @media (min-width: 769px) {
      justify-content: space-between;
    }
  }

  & .form-label {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: var(--field-width);
    flex-grow: 1;

    @media (min-width: $xs) {
      width: calc(50% - 2.5rem);
      margin-right: 0.9rem;
    }
    @media (min-width: 769px) {
      width: 100%;
      margin-right: 0;
    }
  }

  & .form-control {
    align-items: flex-start;
  }

  & .form-select {
    margin-bottom: 0;
    min-height: 46px;

    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: rgba(66, 71, 77, 0.5);
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }

  & button[type='submit'] {
    margin-top: 0;
    padding: 16px 29px;
    max-width: 100%;
    text-align: center;
    width: 100%;
    @media (min-width: $xl) {
      padding-top: 0.8rem;
      padding-bottom: 0.8rem;
    }
    @media (min-width: $xxl) {
      margin-right: auto;
      margin-left: 0;

      padding: 0.8rem 4.2rem;
    }
  }

  &__btns {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: auto;
    margin-bottom: auto;
    padding-right: 0;
    padding-top: 1rem;
    width: var(--field-width);
    width: 100%;

    @media (min-width: $xs) {
      padding-top: 35px;
      padding-right: 0.9rem;
      width: calc(50% - 0.5rem);
    }
    @media (min-width: $sm) {
      padding-right: 1.7rem;
    }
    @media (min-width: 769px) {
      width: 100%;
      padding-right: 0;
      padding-top: 0.5rem;
    }
  }
}
