.map {
  position: relative;
  width: 100%;
  min-height: 800px;
  background-color: $blue-color;
  background-image: url('../img/clubs/map.jpg');
  background-size: cover;
  border-bottom-left-radius: 22px;
  border-bottom-right-radius: 22px;
  overflow: hidden;

  & .container {
    position: relative;
    overflow: hidden;
    height: 100%;
    min-height: 800px;
  }

  &__ya {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  & [class$='-custom-scroll'] {
    box-shadow: -1px 4px 24px rgba(0, 0, 0, 0.2);
    border-radius: 22px;
    border: none;
    overflow: hidden;
  }

  & [class$='-balloon__content'] {
    padding: 0;
    margin: 0;

    & ymaps {
      min-height: 470px;
    }
  }
  & [class$='-balloon__close'] {
    position: absolute;
    top: 5px;
    right: 5px;

    & + [class$='-balloon__content'] {
      margin: 0;
    }
  }
}

#map {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.customControl {
  display: none;
  position: relative;
  background-color: #f6f6f6;
  padding: 0;
  border-radius: 3px;
  max-width: 645px;
  // background: #ffffff;
  box-shadow: -1px 4px 24px rgba(0, 0, 0, 0.2);
  border-radius: 22px;
}

