.form-subscription {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  flex-grow: 1;

  @media (min-width: $xs) {
    align-items: flex-end;
  }
  @media (min-width: $md) {
    align-items: center;
  }


  & .form-label {
    width: 100%;
    @media (min-width: $xs) {
      // margin-bottom: 1rem;
      width: calc(100% / 2 - 2rem);
      margin-right: 2rem;
    }
    @media (min-width: 700px) {
      width: calc(100% / 2 - 30px);
      margin-right: 3rem;
    }
    // @media (min-width: 688px) {
    //   margin-bottom: 1rem;
    // }

    @media (min-width: $sm) {
      width: calc(50% - 35px);
      margin-right: 70px;
      // margin-bottom: 2rem;
    }
    @media (min-width: $md) {
      width: calc(100% / 2 - 30px);
      margin-right: 30px;
      // margin-bottom: 2rem;
      padding-bottom: 4rem;
    }
  }

  & .form-control {
    width: 100%;
  }

  &__btns {
    width: 100%;
    flex-grow: 1;
    @media (min-width: $xs) {
      // margin-top: 20px;
      width: 50%;
    }

    @media (min-width: 700px) {
      width: calc(100% / 2 - 30px);
    }

    @media (min-width: $sm) {
      width: calc(50% - 35px);
    }


  }

  & .button {
    width: 100%;
    box-shadow: $boxShadowdark;

    @media (min-width: $xs) {
      width: auto;
    }
  }
}
