:root {
  --field-width: 100%;

  @media (min-width: $md) {
    --field-width: calc(50% - 0.5rem);
  }
}
.form {
  display: flex;
  flex-direction: column;
  position: relative;

  &__check {
    display: flex;
    align-items: center;
    margin-top: 0.7rem;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: #8f8f8f;
    cursor: pointer;

    @media (min-width: $xl) {
      font-size: 14px;
    }

    &-text {
      display: inline-flex;
      flex-wrap: wrap;
      width: calc(100% - 1.3em);
      padding-left: 0.4rem;

      @media (min-width: $sm) {
        padding-left: 0.8rem;
      }
      @media (min-width: $xl) {
        padding-left: 0.5rem;
      }

      & span {
        display: block;
      }

      & a {
        color: inherit;
        text-decoration: revert;

        &:focus,
        &:hover {
          color: $primary-color;
        }
      }
    }

    @media (min-width: $md) {
      margin-top: 1rem;
      // justify-content: end;
    }
    @media (min-width: $xl) {
      justify-content: stretch;
    }

    .form-check-input {
      width: 1.3em;
      height: 1.3em;
    }
  }

  & .form-label,
  & .form-select,
  & .form-control {
    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: $primary-color;
    padding-bottom: 1.5rem;
    margin-bottom: 0;

    outline-color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 17px,
        768px: 15px,
      )
    );

    @media (min-width: 1024px) {
      padding: 0 0 1rem;
    }

    @media (min-width: $xl) {
      padding-bottom: 1rem;
    }
  }

  &__label-name {
    display: block;
    padding-bottom: 12px;

    @media (min-width: $xl) {
      padding-bottom: 8px;
    }
  }

  & .form-control {
    @include ff(
      'font-size',
      (
        320px: 17px,
        768px: 15px,
      )
    );
  }

  & .form-select,
  & .form-control {
    border-radius: 6px;
    border-color: transparent;
    padding: 1.175rem 0.75rem;
    padding-left: 1.45rem;

    @media (min-width: $xs) {
      padding: 0.775rem 1.45rem;
    }

    @media (min-width: $md) {
      padding: 0.775rem 1.25rem;
    }

    @media (min-width: $xl) {
      padding: 0.8rem 1.25rem;
      padding-left: 1.65rem;
    }

    cursor: pointer;
    &:focus,
    &:hover {
      border-color: $primary-color;
      box-shadow: none;
    }

    &::placeholder {
      font-family: $mainFont;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: rgba(66, 71, 77, 0.5);

      @include ff(
      'font-size',
      (
        320px: 17px,
        768px: 15px,
      )
    );
    }
  }

  & .form-label {
    position: relative;
  }

  & .valid-feedback,
  & .invalid-feedback {
    position: absolute;
    bottom: -4px;
    right: 0;
    text-align: right;
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }
}
